import React, { useEffect, useState } from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import './App.css';
import MovieList from './components/MovieList/MovieList';
import MovieListHeading from './components/MovieListHeading/MovieListHeading';
import SearchBox from './components/SearchBox/SearchBox';
import AddFavorites from './components/AddFavorites/AddFavorites';
import RemoveFavorites from './components/RemoveFavorites/RemoveFavorites';

const App = () => {

  const [movies, setMovies] = useState([]);
  const [searchValue, setSearchValue] = useState('');
  const [favorites, setFavorites] = useState([]);

  const getMovieRequest = async (searchValue) => {
    if(searchValue !== undefined){
      const url= `https://www.omdbapi.com/?s=${searchValue}&apikey=df3047c4`;
      const response = await fetch(url);
      const responseJson = await response.json();
      if(responseJson.Search){
        setMovies(responseJson.Search);
      }
    }
  }

  useEffect(() => {
    getMovieRequest();
  }, []);

  useEffect(() => {
    getMovieRequest(searchValue);
  }, [searchValue]);

  useEffect(() => {
    const favoriteMovies = JSON.parse(localStorage.getItem('react-movie-app-favorites'));
    if(favoriteMovies){
      setFavorites(favoriteMovies);
    }
  }, [])

  const saveToLocalStorage = (items) => {
    localStorage.setItem('react-movie-app-favorites', JSON.stringify(items));
  }

  const addFavoriteMovie = (movie) =>{
    let isInFavoriteList = false;
    const newFavoriteList = [...favorites];
    for(let i=0;i<newFavoriteList.length;i++){
      if(newFavoriteList[i].imdbID === movie.imdbID){
        isInFavoriteList = true;
      }
    }
    if(!isInFavoriteList){
      newFavoriteList.push(movie);
      setFavorites(newFavoriteList);
      saveToLocalStorage(newFavoriteList);
    }
  }

  const removeFavoriteMovie = (movie) => {
    const newFavoriteList = favorites.filter((favorite) => {
      return favorite.imdbID !== movie.imdbID
    });
    setFavorites(newFavoriteList);
    saveToLocalStorage(newFavoriteList);
  }

  return (
    <div className='container-fluid movie-app'>
      <div className='row d-flex align-items-center mt-4 mb-4'>
        <MovieListHeading heading='Movies'/>
        <SearchBox searchValue={searchValue} setSearchValue={setSearchValue}/>
      </div>
      <div className='row'>
        <MovieList 
          movies={movies} 
          favoriteComponent={AddFavorites}
          handleFavoritesClicked={addFavoriteMovie}
        />
      </div>
      <div className='row d-flex align-items-center mt-4 mb-4'>
        <MovieListHeading heading='Favorites'/>
      </div>
      <div className='row'>
        <MovieList 
          movies={favorites}
          favoriteComponent={RemoveFavorites}
          handleFavoritesClicked={removeFavoriteMovie} 
        />
      </div>
    </div>
  );
}

export default App;
